table {
  border-collapse: collapse;
  background-color: transparent;
}

.table {
  line-height: normal;
  width: 100%;
  max-width: 100%;
}

.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #eceeef;
}

.thead-default th {
  font-weight: 700;
  color: #55595c;
  background-color: #fff;
}

.tr-default {
  font-weight: 700;
  color: #fff;
  background-color: #55595c;
}

.table td, .table th {
  padding: .75rem;
  vertical-align: top;
  border-top: 1px solid #eceeef;
}

th {
  text-align: left;
}

.table-responsive{
  display: block;
  width: 100%;
  min-height: 0%;
  overflow-x: auto;
}
