.portfolio-card {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
  position: relative;

  &:after {
    clear: both;
    content: "";
    display: table;
  }

  &__all-link {
    width: 100%;
    padding-top: 10px;
    text-align: center;

    & a {
      display: inline-block;
      padding: 20px 40px;
      background: #3BC391;
      color: #fff;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }

    }

  }

  & h2 {
    //position: absolute;
    //top: -60px;
    color: #333;
    margin: 0;
    padding: 40px 0;
    font-size: 40px;
    font-weight: 400;
  }

  &__item {
    float: left;
    width: 33.33%;
    overflow: hidden;
    position: relative;
    cursor: pointer;

    &:hover &-border__right-top {
      width: 100%;
      transition: 0.2s ease 0s;
    }

    &:hover &-border__right {
      height: 100%;
      transition: 0.2s ease 0.2s
    }

    &:hover &-border__right-bottom {
      width: 100%;
      transition: 0.2s ease 0.4s;
    }

    &:hover &-border__left {
      height: 100%;
      transition: 0.2s ease 0.6s
    }

    &:hover img {
      transform: scale(1.1);
    }

    &:hover &-bg {
      background: rgba(0,0,0,0);
    }

    &-info {
      position: absolute;
      bottom: 0;
      padding: 40px;
      z-index: 2;

      & h3 {
        margin: 0;
        color: #fff;
        font-size: 18px;
        font-weight: 500;
        line-height: normal;
        text-shadow: 1px 1px 1px rgba(0,0,0,.8);
      }

    }

    &-bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;
      background: rgba(0,0,0,.2);
      transition: all 2s ease 0s;
    }

    &-border {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;

      &__right-top {
        width: 0px;
        height: 2px;
        position: absolute;
        top: 0px;
        left: 0%;
        background: #3BC391;
        transition: 0.2s ease 0.6s;
      }

      &__right{
        width: 2px;
        height: 0%;
        position: absolute;
        top: 0px;
        right: 0px;
        background: #3BC391;
        transition: 0.2s ease 0.4s
      }
      &__right-bottom{
        width: 0px;
        height: 2px;
        position: absolute;
        bottom: 0px;
        right: 0px;
        background: #3BC391;
        transition: 0.2s ease 0.2s
      }
      &__left{
        width: 2px;
        height: 0%;
        position: absolute;
        bottom: 0px;
        left: 0px;
        background: #3BC391;
        transition: 0.2s ease 0s
      }
    }

    & img {
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 100%;
      transition: transform 3s ease 0s;
      vertical-align:bottom
    }

  }

}


