/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .portfolio {

    &__cl {
      width: 50%;
    }

    &__cr {
      width: 50%;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .portfolio-card {

    & h2 {
      font-size: 22px;
      padding: 20px;
    }

    &__item {
      width: 50%;

      &-info {

        & h3 {
          font-size: 18px;
          //font-weight: 300;
        }

      }

    }

  }

  .portfolio {

    &__container {
      padding: 10px;
    }

    &__cl {
      width: 100%;
    }

    &__cr {
      width: 100%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {

  .portfolio-card {

    &__item {
      width: 100%;
    }

  }

  .portfolio {

    &__cl {
      width: 100%;
    }

    &__cr {
      width: 100%;
    }

  }
}

