.page {

  &__container {
    padding: 20px;
    margin: 0 auto;
    max-width: 1200px;
    background: #fff;
  }

  &__row {

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__c {

    &-6-12 {
      float: left;
      width: 50%;
    }

  }

  &__title {
    font-size: 36px;
    font-weight: 400;
    margin: 0;
    padding: 20px 0;
  }

  &__info {
    position: relative;
    padding: 20px 0;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

    & img {
      width: 100%;
      padding: 0;
    }

  }

  &__info-text {
    padding: 0 20px 0 0;

    & h2 {
      margin: 0;
    }

  }

  &__cl {
    display: block;
    float: left;
    width: 50%;
  }

  &__cr {
    position: relative;
    display: block;
    float: left;
    width: 50%;
  }

  &__content {
    color: #333;

    & h2 {
      margin-top: 60px;
      line-height: 36px;

      &:first-child {
        margin: 0;
      }

    }

    & hr {
      display: block;
      height: 1px;
      border: 0;
      border-top: 1px solid #ccc;
      margin: 1em 0;
      padding: 0;
    }

    & p {
      padding: 10px 0;
      font-size: 16px;
      line-height: 26px;
    }

    & img {
      width: 100%;
      padding: 0 5px 5px 5px;
    }

    & a {
      color: #0277BD;
      text-decoration: none;
    }

    & ol {
      line-height: 26px;
      padding: 0 20px 20px 20px;
    }

    & ul {
      line-height: 26px;
      padding: 0 20px 20px 20px;
    }


    &__alert {
      padding: 20px;
      margin: 20px 0;
      border: 1px solid transparent;
      background-color: #f2dede;
      border-color: #ebcccc;
      color: #a94442;
    }

  }

}

.page-card {
  //max-width: 1200px;
  //margin: 0 auto;
  //padding: 0 10px;
  position: relative;

  &:after {
    clear: both;
    content: "";
    display: table;
  }

  &__item {
    float: left;
    width: 25%;
    overflow: hidden;
    position: relative;
    cursor: pointer;

    &:hover &-border__right-top {
      width: 100%;
      transition: 0.2s ease 0s;
    }

    &:hover &-border__right {
      height: 100%;
      transition: 0.2s ease 0.2s
    }

    &:hover &-border__right-bottom {
      width: 100%;
      transition: 0.2s ease 0.4s;
    }

    &:hover &-border__left {
      height: 100%;
      transition: 0.2s ease 0.6s
    }

    &:hover img {
      transform: scale(1.1);
    }

    &:hover &-bg {
      background: rgba(0,0,0,0);
    }

    &-info {
      position: absolute;
      top: 0;
      padding: 40px;
      z-index: 2;

      & h3 {
        margin: 0;
        color: #fff;
        font-size: 18px;
        font-weight: 700;
        line-height: normal;
        text-shadow: 1px 1px 1px rgba(0,0,0,.8);
      }

    }

    &-bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;
      background: rgba(0,0,0,.2);
      transition: all 2s ease 0s;
    }

    &-border {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;

      &__right-top {
        width: 0px;
        height: 2px;
        position: absolute;
        top: 0px;
        left: 0%;
        background: #3BC391;
        transition: 0.2s ease 0.6s;
      }

      &__right{
        width: 2px;
        height: 0%;
        position: absolute;
        top: 0px;
        right: 0px;
        background: #3BC391;
        transition: 0.2s ease 0.4s
      }
      &__right-bottom{
        width: 0px;
        height: 2px;
        position: absolute;
        bottom: 0px;
        right: 0px;
        background: #3BC391;
        transition: 0.2s ease 0.2s
      }
      &__left{
        width: 2px;
        height: 0%;
        position: absolute;
        bottom: 0px;
        left: 0px;
        background: #3BC391;
        transition: 0.2s ease 0s
      }
    }

    & img {
      padding: 0 !important;
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 100%;
      transition: transform 3s ease 0s;
      vertical-align:bottom
    }

  }

}



/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .page-card {

    & h2 {
      font-size: 22px;
      padding: 20px;
    }

    &__item {
      width: 50%;

      &-info {

        & h3 {
          font-size: 18px;
          //font-weight: 300;
        }

      }

    }

  }


}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {

  .page-card {

    &__item {
      width: 100%;
    }

  }

}

