.breadcrumb-container {
  margin: 0 auto;
  max-width: 1200px;
  background: #fff;
}

.breadcrumb-arrow {
  height: 36px;
  padding: 0;
  line-height: 36px;
  list-style: none;
}

.breadcrumb {
  margin: 0;
  padding: 10px 20px;

  & li {
    float: left;
    padding-right: 10px;
    color: #777;

    &:after {
      content: "/";
      color: #c6c6c6;
    }

    &:last-child::after {
      content: "";
    }

    & a {
      padding-right: 10px;
      color: #777;
      text-decoration: none;
      transition: color 0.1s;

      &:hover {
        color: #3BC391;
      }

    }

  }

}
